<template>
  <div class="member-box">
    <div class="query-wrapper">
      <div>
        <el-form :inline="true" :model="queryForm" ref="queryForm" label-width="100px">
          <el-form-item label="客户手机号">
            <el-input v-model="queryForm.phone" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="跟进人">
            <el-input v-model="queryForm.enterMan" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
          <el-form-item label="客户姓名">
            <el-input v-model="queryForm.userName" :placeholder="'input' | solvePlaceholder" />
          </el-form-item>
        </el-form>
      </div>
      <div class="query-btn-group mt15">
        <el-button type="primary" @click="query">查询</el-button>
        <el-button @click="reset">重置</el-button>
      </div>
    </div>
    <el-table :data="follows">
      <el-table-column prop="userName" label="跟进时间" />
      <el-table-column prop="userName1" label="客户名称" />
      <el-table-column prop="num" label="跟进人" />
      <el-table-column prop="result" label="跟进结果" />
    </el-table>

    <MemberPagination @callback="queryContent" :total="total" :pages="pages"/>
  </div>
</template>
<script>
  import {Apis} from '@/axios'
  import MemberPagination from '@/components/memberPagination'
  export default {
    name: 'FollowUp',
    data() {
      return {
        queryForm: {
          phone:'',
          enterMan:'',
          userName:'',
          myBriefing:''
        },
        queryFormCopy:{},
        follows: [],
        pages: {
          pageNum: 1,
          pageSize: 10
        },
        total: null
      }
    },
    components:{MemberPagination},
    mounted() {
      let _this = this
      _this.init()
    },
    methods: {
      init() {
        let _this = this
        _this.query()
      },
      query() {
        let _this = this
        _this.queryContent('query')
      },
      handlePagination(type, val) {
        let _this = this
        if (type == 'size') {
          _this.pages.pageSize = val
        } else if(type == 'page') {
          _this.pages.pageNum = val
        }
        _this.queryContent('pagination')
      },
      queryContent(type, _pages) {
        let _this = this
        let {flowerCustomerPage} = Apis.member
        let params = type == 'size' ? {..._this.queryForm} : type == 'pagination' ? {..._this.queryFormCopy}: {}
        params = _pages ? {...params, ..._pages} : {...params, ..._this.pages}
        flowerCustomerPage(params).then(res => {
          if (res.data.status == 200) {
            let {records, total} = res.data.data
            _this.total = total
            _this.follows = records
          } else _this.$message.error(res.data.msg)
        })
      },
      reset() {},

    }
  }
</script>
<style lang="less" scoped>
  .member-box {
    height: 100%;
    overflow-y: auto;
    box-sizing: border-box;
    .query-btn-group{
      text-align: right;
    }
  }
</style>
